html {
  box-sizing: border-box;
  font-family: 'LabGrotesque-Regular', sans-serif; }

*, *::before, *::after {
  box-sizing: inherit;
  transition: 1s; }

a {
  color: inherit;
  text-decoration: none; }

ul, li {
  list-style: none;
  margin: 0;
  padding: 0; }

h1, h2, h3, h4, h5, h6 {
  margin: 0; }

body {
  min-height: 100vh;
  min-width: 100%;
  margin: 0; }

